.footer {
    width: 100%;
    height: auto;
    text-align: right;
    background-color: black;
    padding: 20px 0;
    color: #ffffff;
    &__scroll-go-top {
        background-color: #ff4522;
        opacity: 0;
        transition: opasity 1s;
        width: 45px;
        height: 45px;
        text-align: center;
        border-radius: 50%;
        position: fixed;
        bottom: 20px;
        right: 20px;
        z-index: 99999;
        transition: 0.5s;
        &:hover {
            background-color: #282828;
            color: #fff;
        }
    }
}
