/* SWIPER */

.slider-my {
    position: relative;
    top: 50%;
    &__wrapper {
        height: 100%;
        align-items: center;
    }
    &__slide {
        width: 100%;
        text-align: center;
        font-size: 20px;
        line-height: 36px;
    }
    &__text {
        position: relative;
        z-index: 1;
        text-align: center;
        font-weight: 500;
        @media screen and (max-width: 968px) {
            max-width: 300px;
            margin: 0 auto;
            font-size: 20px;
            font-weight: 500;
        }
        @media screen and (max-width: 520px) {
            max-width: 265px;
        }
        @media screen and (max-width: 376px) {
            max-width: 248px;
            font-size: 18px;
        }
        @media screen and (max-width: 360px) {
            font-size: 15px;
        }
    }
    &_bg {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        margin-right: -50%;
        font-size: 100px;
        z-index: 0;
        opacity: 0.1;
    }
}

/*  */

.slider-my .swiper-button-next::after,
.slider-my .swiper-button-prev::after {
    color: black;
    font-size: 20px;
    font-weight: 800;
    z-index: 0 !important;
}
